import React from 'react';
import '../styles/JobOffer.scss';
export default function JobOffer(props) {
    const arrayTags = props.offer.tags;
    return(
        <div className="JobOffer--container">
            <div className="JobOffer--information-container">
                <img src={require(`../images/${props.offer.companyLogo}`)} alt="Company logo" className="JobOffer--company-logo"/>
                <div className="JobOffer--information">
                <p>{props.offer.companyName} - New!</p>
                <h3>{props.offer.jobTitle}</h3>
                <p>{props.offer.jobPostingDate}  - {props.offer.jobWorkingDay} - {props.offer.jobLocation}  </p>
            </div>
            </div>
        <div className="JobOffer--tags">
            {arrayTags.map((tag)=> {
                return (
                    <p>{tag}</p>
                ) 
            })}
        </div>
        </div>
       
    )

}