function UseSearchJobOffers(jobOffers){
    //inicializamos el query
    const [query,setQuery] = React.useState('');
    //guardamos el dato del filtro que aplicamos a los offers en funcion del query
    const [filteredJobOffers,setFilteredJobOffers] = React.useState(jobOffers);
    //aprovechamos useMemo para guardar valores memorizados y tardar menos
    React.useMemo(()=>{
        const result = jobOffers.filter(jobOffer => {
       return (jobOffer.companyName.toLowerCase().includes(query.toLowerCase())) || (jobOffer.jobTitle.toLowerCase().includes(query.toLowerCase())) || (jobOffer.jobLocation.toLowerCase().includes(query.toLowerCase())) || (jobOffer.jobWorkingDay.toLowerCase().includes(query.toLowerCase()))|| (jobOffer.tags.map((tag)=>{return tag.toLowerCase()}).includes(query.toLowerCase()));
        });
        setFilteredJobOffers(result)
      },[jobOffers, query]);

      return {query, setQuery,filteredJobOffers};
}

function jobOffersList(props){
    const jobOffers = arrayOffers;
    //hook para useState y poder escribir en el input para filtrar los offers
    const {query, setQuery, filteredJobOffers} = UseSearchJobOffers(jobOffers);

    if (filteredJobOffers.length === 0) {
        return(
          
            <div className="">
            <div className="form--job-filter">
            <label>No se han encontrado ofertas! Buscar:</label>
                <input type="text" className="form-control" value={query} onChange={(event)=> {
                    setQuery(event.target.value);}}/>
            </div>
            </div>
        )
}
    return(
        <div className="main--wrapper">
            <div className="header--container">
                <Header />
            </div>
            <div className="form--job-filter">
                <label>Buscar:</label>
                <input type="text" className="form-control" value={query} onChange={(event)=> {
                    setQuery(event.target.value);}}/>
            </div>
        <div className="jobOfferContainer">
        {filteredJobOffers.map((jobOffer)=> {
          return (
              <li key={jobOffer.jobOfferId}>
              <JobOffer offer={jobOffer} />
              </li>
          )  
        }
        )}
        </div>
        </div>
    )
}

export default jobOffersList;